import useInput from "@hooks/useInput";
import { withdrawalUserAction } from "actions/user";
import { Select } from "antd";
import React, { FC, useCallback, useEffect, useState } from "react";
import { mainSlice } from "slices/main";
import { WithdrawalModalWrapper } from "./styles";
const { Option } = Select;
import { useDispatch, useSelector } from "react-redux";
import { RootState } from "slices";
import { toastSuccessMessage } from "config";
import router from "next/router";
import { userSlice } from "slices/user";

interface IProps {}

const WithdrawalModal: FC<IProps> = () => {
  const dispatch = useDispatch();
  const [reason, setReason] = useState("");
  const { withdrawalUserDone } = useSelector((state: RootState) => state.user);
  useEffect(() => {
    if (withdrawalUserDone) {
      toastSuccessMessage("  .  .");
      router.push("/");
      dispatch(userSlice.actions.withdrawalUserClear());
    }
  }, [withdrawalUserDone]);
  const [password, onChangePassword] = useInput("");
  const handleReasonChange = useCallback((value: string) => {
    setReason(value);
  }, []);
  const onClickWithdrawal = useCallback(() => {
    dispatch(withdrawalUserAction({ reason, password }));
  }, [reason, password]);
  return (
    <WithdrawalModalWrapper>
      <h3>  .</h3>
      <Select
        value={reason}
        defaultValue=" "
        onChange={handleReasonChange}
        style={{ width: "100%" }}
      >
        <Option value="    ">    </Option>
        <Option value="     ">     </Option>
        <Option value="    ">    </Option>
        <Option value="   ">   </Option>
        <Option value=" "> </Option>
        <Option value="  ">  </Option>
      </Select>
      <h3> </h3>
      <input
        value={password}
        onChange={onChangePassword}
        className="password-input"
        type="password"
      />
      <p>
               .    
             .
      </p>
      <div className="btn-wrapper">
        <button onClick={() => dispatch(mainSlice.actions.closeModal())}></button>
        <button onClick={onClickWithdrawal}></button>
      </div>
    </WithdrawalModalWrapper>
  );
};

export default WithdrawalModal;
